<template>
    <div class="main">
        <div v-wechat-title="$route.meta.title"></div>
        <!--登录界面-->
        <img class="icon" src="./img/icon.jpg" alt="">
        <div class="userInfo">
			<span>
				账号
			</span>
            <div class="userPhone">
                <img src="./img/phone.png" alt="">

                <input type="number" placeholder="请输入账号" v-model="account">
            </div>
            <span class="none">
				密码
			</span>

            <div class="password">

                <img src="./img/password.png" alt="">
                <!--<input type="text" placeholder="请输入密码" v-model="password">-->
                <i class="icon-password"></i>
                <input type="text" v-if="pwdType" v-model="password"/>
                <input type="password" placeholder="请输入密码" v-model="password" v-else/>
                <img style="left:87%!important;width: 30px;height: 20px" :src="seen ? seenImg : unseenImg"
                     @click="changeType()" v-on:mouseover="hoverEye" v-on:mouseout="outEye" class="icon-eye"/>
            </div>
            <div style="display: flex;align-items: center;height: 20px;">
                <input style="width: 20px;height: 20px;" type="radio" name="role" @change="getRadioVal()" :value="2"
                       checked="true" v-model="role"/>学生
                <input style="width: 20px;height: 20px;margin-left: 20px" type="radio" @change="getRadioVal()"
                       name="role" :value="3" v-model="role"/>家长

            </div>

            <span class="tip">默认密码为手机号后六位，请及时修改密码（如已修改请忽略）</span>

            <div class="reg">
                <button @click="login()">登陆</button>
            </div>
            <span class="tip reg" @click="goReg()">
				没有账号？点击注册
			</span>
        </div>


    </div>

</template>

<script>
    import request from '../../static/js/request';
    import request1 from '../../static/js/request1';
    import result from '../../static/js/result1';

    export default {
        data() {
            return {
                account: '',//账号
                password: '',//密码
                token: '',
                role: 2,//登录类型 2，为学生，3为家长
                seen: "",
                unseenImg: require("./img/see.png"),   //看不见密码时小眼睛的显示图片地址
                seenImg: require("./img/see1.png"),   //看得见密码时小眼睛的显示图片地址
                eyeTxt: "",
                pwdType: false   //此时文本框隐藏，显示密码框
            }
        },
        mounted() {

        },
        created() {
            console.log(this.role)
            localStorage['edit'] = false;
            // console.log('标题',this.$route.meta.title)
            // console.log('进到登陆');
            // console.log('用户手机号--------',localStorage['phone'])
            //this.getUserInfo();

        },
        methods: {
            changeType: function () {
                this.seen = !this.seen;   //小眼睛的变化
                this.pwdType = !this.pwdType;   //跟着小眼睛变化，密码框隐藏，显示文本框 内容就显示了
            },
            hoverEye: function (event) {
                this.seen = !this.seen;
            },
            outEye: function (event) {
                this.seen = !this.seen;
            },
            getRadioVal() {
                console.log(this.role)
            },
            /**
            *   登录
            * */
            async login() {
                if (this.account == '') {
                    weui.alert("请输入账号！");
                    return false;
                }
                if (this.password == '') {
                    weui.alert("请输入密码！");
                    return false;
                }
                if (this.role == 0) {
                    weui.alert("请选择角色！");
                    return false;
                }

                let params = {
                    account: this.account,
                    password: this.password,
                    loginType: this.role
                }
                let loginInfo = await request('/api/open/token',
                    'post', params, 'application/x-www-form-urlencoded')
                // loading.hide()
                let res = result(loginInfo)
                console.log('失败信息', loginInfo)
                if (loginInfo.msg == '用户或密码错误') {
                    weui.alert(loginInfo.msg)
                }
                // loading.hide()
                if (!res) return;
                console.log('登陆成功信息', loginInfo)
                this.$store.commit('setToken', loginInfo.msg);//将token存入vuex中
                this.$store.commit('setRole', this.role);//将登录类型存入
                this.$store.commit('setIsLogin', true);
                console.log('token值', this.$store.state.token);
                this.getUserInfo();//获取用户信息
            },
            /**
             * 获取账号信息，
             * **/
            async getUserInfo() {
                let params = {
                    access_token: this.$store.state.token
                }

                /*let headers = {
                    contentType:'application/x-www-form-urlencoded',
                    //token:this.$store.state.token

                }*/
                let getUserInfo = await request('/api/token/user/userId',
                    'post', params, 'application/x-www-form-urlencoded')
                // loading.hide()
                let info = result(getUserInfo)
                console.log('获取电话号失败信息', getUserInfo)
                // loading.hide()
                if (getUserInfo.code == 500) {
                    this.$store.commit('setPhone', '');
                }
                if (!info) return;
                console.log('获取电话号成功信息', getUserInfo)
                this.$store.commit('setPhone', getUserInfo.msg);
                this.$router.go(-1)
            },
            goReg() {
                this.$router.push('/regStudent')
            }
        },
        watch: {}
    }
</script>

